<template>
    <div class="info-bar">
        <div class="bar-item">
            <div class="bar-img"></div>
            <div class="bar-content">
                <h1>学历</h1>
                <p>本科</p>
            </div>
        </div>

        <div class="bar-item">
            <div class="bar-img"></div>
            <div class="bar-content">
                <h1>年龄</h1>
                <p>24</p>
            </div>
        </div>

        <div class="bar-item">
            <div class="bar-img"></div>
            <div class="bar-content">
                <h1>坐标</h1>
                <p>深圳</p>
            </div>
        </div>

        <div class="bar-item">
            <div class="bar-img"></div>
            <div class="bar-content">
                <h1>状态</h1>
                <p>在职</p>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'tag'
    }
</script>
<style lang="scss">

    .info-bar{
        width: 80%;
        display: flex;
        background: rgba(107,184,190, 1);
        border-radius: 40px;
        padding: 40px;
        margin:10vh auto 0px auto;
        max-width: 900px;
        letter-spacing: 2px;
        .bar-item{
            width: 25%;
            font-size: 18px;
            color: #ffffff;
            .bar-img{
                width: .8rem;
                height: .8rem;
                margin: 0 auto;
            }
            .bar-content{
                width: .9rem;
                position: relative;
                margin: 10px auto 0px auto;
                h1{
                    text-align: left;
                }
                p{
                    &:before{
                        content: "";
                        display: inline-block;
                        width: 100%;
                        height: 2px;
                        background: #ffffff;
                        position: absolute;
                        bottom:0;
                        left: 0;
                        transform-origin: 0 center;
                        transform: rotate(-21deg);
                    }
                    text-align: right;
                }
            }
            &:nth-of-type(1){
                .bar-img{
                    background: url("../../assets/a1.png") no-repeat center center;
                    background-size: .5rem .5rem;
                    border:2px solid #ffffff;
                    border-radius: 50%;
                }
            }
            &:nth-of-type(2){
                .bar-img{
                    background: url("../../assets/a2.png") no-repeat center center;
                    background-size: .5rem .5rem;
                    border:2px solid #ffffff;
                    border-radius: 50%;
                }
            }
            &:nth-of-type(3){
                .bar-img{
                    background: url("../../assets/a3.png") no-repeat center center;
                    background-size: .5rem .5rem;
                    border:2px solid #ffffff;
                    border-radius: 50%;
                }
            }
            &:nth-of-type(4){
                .bar-img{
                    background: url("../../assets/a4.png") no-repeat center center;
                    background-size: .5rem .5rem;
                    border:2px solid #ffffff;
                    border-radius: 50%;
                }
            }
        }
    }
</style>



