<template>
  <div id="app" :class="'swipper'+show">
    <section class="p-self section">
      <transition name="section1">
      <article v-if="show==1">
        <header><img src="./assets/psb.jpg" width="200" ></header>
        <hr>
        <section>
          <h1>我叫杨煜东</h1>
          <h1>一个PHP程序员，会点前端</h1>
          <h1 class="p-email">
            youngyudong@foxmail.com
          </h1>
        </section>
      </article>
      </transition>
    </section>
    <section class="p-introduce section">
      <div class="introduct-wampper">
        <header>
          <h1>关于我</h1>
          <p>(ABOUT ME)</p>
        </header>
        <v-tag></v-tag>
        <v-text></v-text>
      </div>
    </section>
    <section class="p-experience section">
        <div class="experience-wampper">
            <header>
                <h1>技能篇</h1>
                <p>(SKILL)</p>
            </header>
            <div class="experience-box">
                <v-tech :tecName="php"></v-tech>
                <v-tech :tecName="js"></v-tech>
            </div>
            <div class="experience-introduce">
                <p>
                    前端确实覆盖十分广泛，自己主要的方向是Js原生语法;
                    Jquery,Bootstrp在实习的时候运用得比较多Bootstrp;
                    现在主要是Vue + vue-router + vuex;
                    后端PHP框架用过ThinkPHP在一个基于Symfony的系统（Edusoho）进行过二次开发;
                    Nginx也有接触数据库Mysql，Redis
                </p>
            </div>
        </div>
    </section>
    <section class="p-comment section">
        <div class="comment-wrampper">
            <header>
                <h1>工作经验</h1>
                <p>(EXPERIENCE)</p>
            </header>
            <div class="comment-box">
                <div class="comment-item" v-for="(item,index) in experience" v-if="exShow===index">
                    <h3>{{item.company}}</h3>
                    <p class="p-time">{{item.time}}</p>
                    <p class="p-job">{{item.job}}</p>
                    <ul>
                        <li v-for="value in item.content" v-html="value"></li>
                    </ul>
                </div>
            </div>
            <nav>
                <ul>
                    <li :class="{'selected':exShow===0}" @click="select(0)"></li>
                    <li :class="{'selected':exShow===1}" @click="select(1)"></li>
                    <li :class="{'selected':exShow===2}" @click="select(2)"></li>
                </ul>
            </nav>
        </div>
    </section>
  </div>
</template>

<script>
import VTag from '@/components/tag/tag';
import VText from '@/components/tag/text';

import VTech from '@/components/tag/tech';
export default {
  name: 'App',
  components: {
  },
  data () {
      return {
        exShow: 2,
        show: 4,
        php: {
            name: 'PHP',
            list: ['Laravel','Mysql','Redis','Apache','Shell','ThinkPHP']
        },
        js: {
           name: '前端',
           list: ['Vue','Git','Scss','Webpack','jQuery','AJAX']
        },
        experience:[
            {
                company: '重庆速率科技有限公司',
                time: '2016年1月份-2016年6月份',
                job: '前端开发',
                content: [
                    '主要负责网站首页制作，banner图片制作',
                    '技术栈：主要Jquery + Bootstrp',
                    '代表作：<a href="http://www.0856r.com/">速率科技官网</a>',
                    '两年前的作品，不要见怪'
                ]
            },
            {
                company: '重庆小薇金服有限公司',
                time: '2016年6月份-2016年11月份',
                job: 'PHP|前端开发',
                content: [
                    '主要是Edusoho的系统二次开发',
                    '当时遇到了很多的难点的，刚从学校毕业，没用过的Symfony框架，当时自己对前端require也是懵懵懂懂',
                    '边学边用还是完成的包括积分制，批量操作用户等功能',
                    '现已经停止运作'
                ]
            },
            {
                company: '深圳烁途科技有限公司',
                time: '2016年11月份-至今',
                job: '全栈工程师',
                content: [
                    '代表作：<a href="https://www.exwayboard.com/index.php/Ch">烁途科技官网</a>  ',
                    '技术栈：前期ThinkPHP+jQuery,后期：vue全家桶+lamp+laravel+redis',
                    '之所以选择换技术栈，因为前期只是单纯的展示型网站，后期还设计了商城，社区功能，为了给自己一些挑战，拓展下自己技术线',
                    '主要任务：基础功能的开发，网站后台采用element-ui+vue',
                    '其它任务：开发APP接口'
                ]
            }
        ],
      };
  },
  created () {
//    setTimeout(() => {
//        this.show = 1;
//    }, 500);
  },
  mounted () {
    this.$nextTick(() => {
        window.onmousewheel = (enent) => {
          enent = event | window.event;
          if (event.wheelDelta > 0) {
            this.__swipperUp();
          } else {
            this.__swipperDown();
          }
        }
    });
  },
  methods: {
      __swipperUp () {
        if (this.show > 1 && !this.going) {
          this.show--;
          this.going = true;
          setTimeout(() => {
            this.going = false;
          }, 1000);
        }
      },
      __swipperDown () {
          let app = document.getElementById('app');
          let len = app.children.length;
          if (this.show < len && !this.going) {
              this.show++;
              this.going = true;
              setTimeout(() => {
                this.going = false;
              }, 1000);
          }
      },
      select (index) {
          this.exShow = index;
      }
    },
  components: {
      VTag, VText, VTech
  }
}
</script>

<style lang="scss">
@import "assets/reset.css";
$color:rgb(107,184,190);
body,html{
  height: 100%;
  width: 100%;
  font-size: 100px;
  position: relative;
  overflow: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  transition: all .5s;
  .section{
    height: 100vh;
      display: flex;
      align-items: center;
      justify-content: center;
    &.p-self{
      background: $color;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
      article{
        flex: 0 0 auto;
        text-align: center;
        header{
          text-align: center;
          >img{
            vertical-align: top;
            width: 2rem;
            height: 2rem;
            border-radius: 50%;
          }
        }
        section{
          margin-top: .1rem;
          font-size: .18rem;
          color: #fff;
          letter-spacing: 2px;
          line-height: 30px;
        }
      }
    }
    .section1-enter,.section1-leave-to{
      margin-top: 1rem;
      opacity: 0;
    }
    .section1-enter-active,.section1-leave-active{
      transition:all 1s;
    }
    &.p-introduce{
      display: flex;
      align-items: center;
      flex-direction: row;
      justify-content: center;
      .introduct-wampper{
          width: 80%;
        header{
          text-align: center;
          h1{
            font-size: .24rem;
            color: $color;
          }
          p{
            font-size: .18rem;
            &:before{
              content: "";
              width: 100%;
              height: 2px;
              background: #000000;
              display: block;
              margin: 10px auto;
              max-width: 2rem;
            }
          }
        }
      }
    }
    &.p-experience{
      background: rgba(107,184,190, 1);
      .experience-wampper{
          width: 90%;
          max-width: 900px;
          color: #ffffff;
          .experience-introduce{
              margin-top: 5vh;
              p{
                  font-size: 18px;
                  text-align: center;
                  line-height: 30px;
                  letter-spacing: 2px;
              }
          }
              header{
                  text-align: center;
                  h1{
                      font-size: 24px;
                  }
                  p{
                      font-size: 18px;
                      &:before{
                          content: "";
                          height: 2px;
                          display: block;
                          width: 100%;
                          max-width: 200px;
                          background: #ffffff;
                          margin: 10px auto;
                      }
                  }
              }
              .experience-box{
                  display: flex;
                  justify-content: space-around;
                  margin-top: 10vh;
              }
          }
    }
    &.p-comment{
        .comment-wrampper{
            width: 80%;
            max-width: 900px;
            header{
                h1{
                    font-size: 24px;
                    text-align: center;
                    color: $color;
                }
                p{
                    font-size: 18px;
                    text-align: center;
                    color: #000000;
                    &:before{
                        content: "";
                        display: block;
                        width: 100%;
                        max-width: 200px;
                        margin: 10px auto;
                        height: 2px;
                        background: #000000;
                    }
                }
            }


            .comment-box{
                height: 300px;
                width: 100%;
                background: $color;
                padding: 20px;
                border-radius: 20px;
                margin-top: 5vh;
                color: #ffffff;
                letter-spacing: 1.5px;
                overflow: hidden;
                .swipperEx-enter,.swipperEx-leave-to{
                    opacity: 0;
                }

                .swipperEx-enter-active,.swipperEx-leave-active{
                    transition: all 0.5s;
                }
                .comment-item{
                    h3{
                        font-size: 24px;
                        line-height: 48px;
                    }
                    .p-time{
                        font-size: 16px;
                        line-height: 32px;
                        color: #666666;
                    }
                    .p-job{
                        font-size: 16px;
                        line-height: 32px;
                    }
                    li{
                        font-size: 14px;
                        line-height: 20px;
                    }
                }

            }
            nav{
                margin-top: 2vh;
                ul{
                    display: flex;
                    justify-content: space-around;
                    li{
                        display: inline-block;
                        width: 10px;
                        height: 10px;
                        border:1px solid #e2e2e2;
                        transform: rotate(45deg);
                        cursor: pointer;
                        &:hover{
                            background: $color;
                            border: none;
                        }
                        &.selected{
                            background: $color;
                            border: none;
                        }
                    }
                }
            }
        }
    }
  }
}
.swipper1{
  transform:translateY(0vh);
}
.swipper2{
  transform:translateY(-100vh);
}
.swipper3{
  transform:translateY(-200vh);
}
.swipper4{
  transform:translateY(-300vh);
}
.swipper5{
  transform:translateY(-400vh);
}
</style>



